import { CheckCircle2, XCircle } from "lucide-react";

interface HistoryProps {
  history: {
    date: string;
    status: string;
  }[];
}

export default function AttendanceTable({ history }: HistoryProps) {
  return (
    <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden h-full">
      <div className="px-6 py-4 border-b border-gray-100 flex justify-between items-center">
        <h3 className="font-semibold text-gray-900">Attendance History</h3>
        <button className="text-sm text-indigo-600 font-medium hover:text-indigo-700">
          View All
        </button>
      </div>
      <div className="overflow-x-auto">
        <table className="w-full text-sm text-left">
          <thead className="bg-gray-50 text-gray-600 font-medium">
            <tr>
              <th className="px-6 py-3">Date</th>
              <th className="px-6 py-3">Status</th>
              <th className="px-6 py-3 text-right">Day</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-100">
            {history.map((record, idx) => (
              <tr key={idx} className="hover:bg-gray-50 transition-colors">
                <td className="px-6 py-4 font-medium text-gray-900">
                  {new Date(record.date).toLocaleDateString("en-US", {
                    month: "short",
                    day: "numeric",
                    year: "numeric",
                  })}
                </td>
                <td className="px-6 py-4">
                  {record.status === "PRESENT" ? (
                    <span className="inline-flex items-center gap-1.5 px-2.5 py-1 rounded-full text-xs font-medium bg-emerald-50 text-emerald-700 border border-emerald-100">
                      <CheckCircle2 className="w-3.5 h-3.5" />
                      Present
                    </span>
                  ) : (
                    <span className="inline-flex items-center gap-1.5 px-2.5 py-1 rounded-full text-xs font-medium bg-rose-50 text-rose-700 border border-rose-100">
                      <XCircle className="w-3.5 h-3.5" />
                      Absent
                    </span>
                  )}
                </td>
                <td className="px-6 py-4 text-right text-gray-500">
                  {new Date(record.date).toLocaleDateString("en-US", {
                    weekday: "long",
                  })}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}